<template>
	<div>
		<div class="app-head">
			<router-link :to="{path:'/'}">
				<img src="../assets/logo.png" class="logo" />
			</router-link>
			<ul>
				<li>{{userName}}</li>
				<li v-if="userName!==''" @click="quitLog"></li>
				<li v-if="userName===''" @click="regClick"></li>
				<li v-if="userName===''">|</li>
				<li v-if="userName===''" @click="logClick"></li>
				<li>|</li>
				<li @click="aboutClick"></li>
			</ul>
		</div>
		<div class="app-content">
			<keep-alive>
				<router-view></router-view>
			</keep-alive>
		</div>
		<div class="app-foot">
			<p>@2018 vue</p>
		</div>
		<my-dialog :is-show="isShowAboutDialog" @on-close="closeDialog('isShowAboutDialog')">
			<p>2015002555300MSCI50A 373737GAMES VR 1000 

			</p>
		</my-dialog>
		<my-dialog :is-show="isShowLogDialog" @on-close="closeDialog('isShowLogDialog')">
			<logForm @has-log="onSuccessLog"></logForm>
		</my-dialog>
		<my-dialog :is-show="isShowRegDialog" @on-close="closeDialog('isShowRegDialog')">
			<regForm></regForm>
		</my-dialog>
	</div>
</template>

<script>
	import Dialog from './base/dialog'
	import logForm from './logForm'
	import regForm from './regForm'
	export default {
		components: {
			MyDialog: Dialog,
			logForm,
			regForm
		},
		data() {
			return {
				isShowAboutDialog: false,
				isShowLogDialog: false,
				isShowRegDialog: false,
				userName: ''
			}
		},
		methods: {
			aboutClick() {
				this.isShowAboutDialog = true
			},
			closeDialog(attr) {
				this[attr] = false
			},
			logClick() {
				this.isShowLogDialog = true
			},
			regClick() {
				this.isShowRegDialog = true
			},
			onSuccessLog(data) {
				this.userName = data.userName
				this.closeDialog('isShowLogDialog')
			},
			quitLog() {
				this.userName = ''
			}
		}
	}
</script>

<style lang="less">
	html,
	body {
		margin: 0;
		padding: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		background: ghostwhite;
	}
	
	li {
		list-style-type: none;
	}
	
	a {
		text-decoration: none;
	}
	ul{
		    -webkit-margin-before: 0em;
    -webkit-margin-after: 0em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    -webkit-padding-start: 0px;
	}
	a:link,
	a:visited,
	a:hover {
		color: #000000;
	}
	
	.app-head {
		width: 100%;
		height: 100px;
		background: dimgray;
		position: relative;
		overflow: hidden;
		.logo {
			width: 100px;
			margin-left: 200px;
		}
		ul {
			position: absolute;
			right: 20px;
			top: 20px;
			li {
				color: white;
				list-style-type: none;
				display: inline-block;
				cursor: pointer;
			}
		}
	}
	
	.app-content {
		width: 1600px;
		overflow: hidden;
		height: auto;
		margin: 0 auto;
	}
	
	.app-foot {
		width: 100%;
		height: 100px;
		background: dimgray;
		color: white;
		overflow: hidden;
		p {
			width: 100%;
			text-align: center;
			margin-top: 50px;
		}
	}
	
	.button {
		background: #4fc08d;
		color: #fff;
		display: inline-block;
		cursor: pointer;
	}
	
	.button:hover {
		background: #4fc08d;
	}
	
	.g-form-line {
		padding: 15px 0;
	}
	
	.g-form-label {
		width: 100px;
		font-size: 16px;
		display: inline-block;
	}
	
	.g-form-input {
		display: inline-block;
	}
	
	.g-form-input input {
		height: 30px;
		width: 200px;
		line-height: 30px;
		vertical-align: middle;
		padding: 0 10px;
		border: 1px solid #ccc;
	}
	
	.g-form-btn {
		padding-left: 100px;
		.button {
			padding: 10px 20px;
		}
	}
	
	.g-form-error {
		color: red;
		padding-left: 15px;
	}
</style>


